.l-grid {
    padding: 0 10px;
    position: relative;
    @include clearfix;
    max-width: 480px;
    margin: 0 auto;
}

.wrapper {
    @include clearfix;
    position: relative;
}

.header {}

.body {
    @include clearfix();
}

.main-content {
    padding-top: 10px;
    @include clearfix();
    .timeline-stories:first-child{
        margin-top: 0;
    }
}

.footer {
    margin-top: 30px;
}


// >  CODER REQUEST CODE
