/*
* TABLE OF CONTENTS
*
* header style
*/

// > header style
.header {
    background-color: $first-color;
    color: #fff;
    .l-grid {
        @include clearfix();
        height: 70px;
    }
    &__logo {
        display: block;
        font-size: 0;
        float: left;
        a{
            height: 70px;
            width: 126px;
            display: inline-block;
        }
        i{
            position: absolute;
            top: 13px;
            left: 10px;
            font-size: 0;
        }
        &:hover {
            opacity: .8;
        }
    }
    &__search {
        float: right;
        width: 48px;
        .btn{
            width: 48px;
            border: none;
            height: 70px;
            padding: 0;
            background-color: transparent;
            vertical-align: top;
            i{
                position: absolute;
                right: 10px;
                top: 23px;
                vertical-align: top;
            }
        }
        .search{
            display: none;
            margin-left: 15px;
            height: 30px;
            width: 230px;
            background: #FFFFFF;
            border-radius: 4px;
            overflow: hidden;
            color: #7f7f7f;
            >*{
                float: left;
            }
            button{
                width: 30px;
                height: 30px;
                border: none;
                padding: 0;
                background-color: transparent;
            }
            input{
                border: none;
                width: 200px;
                height: 30px;
                padding-left: 10px;
            }
        }
    }
}