/*
* TABLE OF CONTENTS
*
* box heading
* abf
* timeline-stories
* box-rank-1
* box-rank-2
* miss-box
*/

// > box heading
.box-heading {
    background: #F4F4F4;
    border-top: 1px solid #D9D9D9;
    height: 38px;
    margin-left: -10px;
    margin-right: -10px;
    padding: 0 10px;
    .main {
        @include font(18, 38);
        text-transform: uppercase;
        float: left;
        i {
            display: none;
            vertical-align: top;
            margin-right: 10px;
        }
        span {
            display: inline-block;
        }
    }
    .sub {
        float: right;
        text-align: right;
        >* {
            display: inline-block;
            &:before {
                content: '/';
                @include font(12, 38, $normal);
                width: 20px;
                height: 37px;
                display: inline-block;
                vertical-align: top;
                text-align: center;
            }
            &:first-child:before {
                display: none;
            }
        }
        a {
            display: inline-block;
            @include font(12, 38, $normal);
            padding-left: 10px;
            margin-left: 10px;
            @include first-x;
        }
    }
    &.theme-color {
        background-color: $first-color;
        color: #fff;
        span,
        a,
        &:after {
            color: #fff;
        }
    }
}

// > abf
.abf {}
.box-normal{}


// > timeline-stories
.timeline-stories {
    .story {
        @include first-y;
        padding-top: 14px;
    }
    .story__heading {
        @include font(12, 16);
        .text {}
    }
    .story__cate {
        margin-top: 4px;
    }
    .story__thumb {
        width: 90px;
        height: 90px;
        @include runaround(left, 2, 12, 0, 0)
    }
    /*--------*/
    .story--infographic {
        .story__cate {
            background-image: linear-gradient(-90deg, #3023AE 0%, #53A0FD 48%, #B4EC51 100%);
        }
    }
    .story--big {
        .story__thumb {
            width: 100%;
            height: 0;
            padding-bottom: 55.4%;
            overflow: hidden;
            margin: 0 0 10px;
            img {
                width: 100%;
                height: auto;
            }
        }
        .story__heading {
            @include font(18, 22);
            .text {}
        }
        .story__meta {
            display: inline-block;
        }
        .story__cate {
            margin-top: 2px;
            margin-left: 10px;
        }
    }
    .story--more-imgs {
        .story__link {
            overflow: hidden;
        }
        .story__thumb {
            width: 392px !important;
            background-color: inherit !important;
            margin-right: 0 !important;
            float: inherit;
            display: block;
            margin-bottom: 4px;
            position: relative;
            img {
                width: 90px;
                height: 90px;
                margin-right: 8px;
                float: left;
            }
        }
        .story__heading {
            @include font(18, 22);
            .text {}
        }
        .story__meta {
            display: inline-block;
        }
        .story__cate {
            margin-top: 2px;
            margin-left: 10px;
        }
    }
    .story--border {
        border-top: 1px solid #e8e8e8;
        padding-top: 10px;
        margin-top: 10px;

        .story__thumb {
            display: none;
        }
    }
}

// > box-rank-1
// piority-2, small column
.box-rank-1 {
    @include clearfix();
    .box-heading {
        background-color: #1264C7;
        color: #fff;
        a {
            color: #fff;
        }
        .sub {
            font-size: 0;
        }
    }
    .box-content {
        margin-left: -10px;
        margin-right: -10px;
        padding: 10px;
    }
    .first-story {
        .story__thumb {
            width: 100%;
            height: 0;
            padding-bottom: 55.4%;
            overflow: hidden;
            margin: 0 0 10px;
            img {
                width: 100%;
                height: auto;
            }
        }
        .story__heading {
            @include font(18, 22);
            .text {}
        }
        .story__meta {
            display: inline-block;
        }
        .story__cate {
            margin-top: 2px;
            margin-left: 10px;
        }
    }
    .list-stories {
        padding-top: 6px;
        .story {
            @include first-y;
            padding-top: 14px;
            @include clearfix();
        }
        .story__heading {
            @include font(12, 16);
            .text {}
        }
        .story__cate {
            margin-top: 4px;
        }
        .story__thumb {
            width: 90px;
            height: 90px;
            @include runaround(left, 2, 12, 0, 0)
        }
    }
    .four-stories {
        padding-top: 6px;
        @include clearfix();
        .story {
            width: calc(50% - 4px);
            float: left;
            margin-top: 16px;
            &:nth-child(1),
            &:nth-child(2) {
                margin-top: 0px;
            }
            &:nth-child(2n) {
                float: right;
            }
            &:nth-child(2n + 1) {
                clear: left;
            }
        }
        .story__thumb {
            width: 100%;
            height: 0;
            padding-bottom: 55.4%;
            overflow: hidden;
            margin: 0 0 10px;
            img {
                width: 100%;
                height: auto;
            }
        }
        .story__heading {
            @include font(12, 16);
            font-weight: 600;
            margin-bottom: 5px;
            .cate {
                margin-top: 2px;
            }
        }
        .story__meta {}
        .story__summary {
            margin-top: 10px;
        }
    }
    &.theme-red {
        .box-content {
            background-color: #C71212;
        }
        color: #fff;
        a {
            color: #fff;
        }
    }
    &.theme-green {
        .box-content {
            background-color: #12C77E;
        }
        color: #fff;
        a {
            color: #fff;
        }
        .story__cate {
            background-color: darken(#12C77E, 10%);
        }
    }
}

// > box-rank-2
// piority-2, small column
.box-rank-2 {
    @include clearfix();
    .box-heading {
        background-color: #1264C7;
        color: #fff;
        a {
            color: #fff;
        }
        .sub {
            font-size: 0;
        }
    }
    .box-content {
        margin-left: -10px;
        margin-right: -10px;
        padding: 10px;
    }
    .first-story {
        .story__thumb {
            width: 100%;
            height: 0;
            padding-bottom: 55.4%;
            overflow: hidden;
            margin: 0 0 10px;
            img {
                width: 100%;
                height: auto;
            }
        }
        .story__heading {
            @include font(18, 22);
            .text {}
        }
        .story__meta {
            display: inline-block;
        }
        .story__cate {
            margin-top: 2px;
            margin-left: 10px;
        }
        .story--video{
            .story__thumb{
                &:after{
                    position: absolute;
                    width: 0;
                    height: 0;
                    border-top: 20px solid transparent;
                    border-bottom: 20px solid transparent;
                    border-left: 30px solid rgba(255, 255, 255, .7);
                    content: '';
                    top: calc( 50% - 10px);
                    left:  calc( 50% - 10px);
                }
            }
        }
    }
    .more-stories {
        @include clearfix();
        margin-top: 5px;
        .story {
            float: left;
        }
        .story__heading {
            @include font(12, 16, $normal);
            max-height: 48px;
            overflow: hidden;
        }
        .story__cate {
            margin-top: 4px;
        }
        .story__thumb {
            width: 48px;
            height: 48px;
            @include runaround(left, 2, 8, 0, 0)
        }
    }
    &.theme-black {
        .box-content {
            background-color: #444;
        }
        color: #fff;
        a {
            color: #fff;
        }
        .story__cate {
            background-color: darken(#444, 10%);
        }
    }
    &.theme-blue {
        .box-content {
            background-color: #1264C7;
        }
        color: #fff;
        a {
            color: #fff;
        }
        .story__cate {
            background-color: darken(#1264C7, 10%);
        }
    }
    .slider-pager{
        text-align: center;
        font-size: 0;
        .bx-pager-item{
            display: inline-block;
            padding-top: 10px;
            a {
                display: block;
                width: 6px;
                height: 6px;
                margin: 0 3px;
                text-indent: -999em;
                border-radius: 50%;
                background-color: #999;
            }
            .active{
                background-color: #fff ;
            }
        }
    }
}

// > miss-box
.box-miss {
    background-image: url('../img/miss-bg.png');
    background-size: 768px 1000px;
    border-radius: 8px;
    background-repeat: no-repeat;
    color: #fff;
    padding-bottom: 20px;
    a {
        color: #fff;
        &:hover {
            color: #fff;
        }
    }
    &__heading {
        text-align: center;
        .main {
            position: relative;
            padding-top: 40px;
            i {
                position: absolute;
                display: block;
                left: 50%;
                margin-left: -39px;
                top: -20px;
            }
            @include font(24,
            30,
            $normal);
            text-transform: uppercase;
        }
    }
    &__content {
        margin-top: 15px;
        position: relative;
        @include clearfix();
    }
    .big-story {
        margin-bottom: 16px;
        .story {
            margin-top: 20px;
            &:first-child {
                margin-top: 0;
            }
        }
        .story__thumb {
            width: 100%;
            height: 0;
            padding-bottom: 55.4%;
            overflow: hidden;
            margin: 0px;
            img {
                width: 100%;
                height: auto;
            }
        }
        .story__heading {
            @include font(18,
            22,
            $normal);
            padding: 10px 16px 0;
        }
        .story__meta {
            padding: 5px 16px 0;
            time {
                &:after {
                    background-color: #fff;
                }
            }
            >* {
                &:after {
                    background-color: #000;
                }
            }
        }
        .story__summary {}
    }
}