/*
* TABLE OF CONTENTS
*
* footer style
*/

// > footer style
.footer {
    position: relative;
    @include clearfix();
    &:before{
        content: '';
        width: 100px;
        height: 1px;
        margin: 0 auto 20px;
        display: block;
        background-color: #000;
    }
    &__copyRight{
        @include font(12,16,$normal);
        text-align: center;
        .main{
            color: #000000;
            font-size: 12px;
            margin-bottom: 20px;
        }
        .more{
            color: #ddd;
            a{
                color: #ddd;
            }
        }
    }
}
